import React from 'react'
import classNames from 'classnames'
import styles from './Buttons.module.css'
import { Link } from 'gatsby'

const PrimaryButton = ({ text, destination = null, handleOnClick = null, square = false, target = null }) => {
    const className = classNames(styles.PrimaryButton, square && styles.Square)

    if (destination) {
        if (target === '_blank') {
            return <a href={destination} target={target} className={className}><button>{text}</button></a>
        }
        return (
            <Link to={destination} target={target} className={className}><button>{text}</button></Link>
        )
    }

    return (
        <button className={className} onClick={handleOnClick}>{text}</button>
    )
}

export default PrimaryButton