import React from 'react'
import MediaIconProvider from '../ImageProviders/MediaIconProvider'
import styles from './HighlightCard.module.css'
import PrimaryButton from '../Buttons/PrimaryButton';

const HighlightCard = ({ card }) => {
    const options = card.frontmatter
    const html = card.html
    const featuredImg = options.featuredImage && options.featuredImage.childImageSharp.fluid.src

    return (
        <MediaIconProvider render={(mediaIcon) => (
            <div className={styles.Container}>
                <div className={styles.ImageBox}>
                    <img src={featuredImg || mediaIcon} alt="media" />
                    <PrimaryButton text={options.button_text} destination={options.button_destination} target='_blank' square />
                </div>
                <div className={styles.ContentBox}>
                    <h2>{options.title}</h2>
                    <div className={styles.RawContent} dangerouslySetInnerHTML={{ __html: html }} />
                </div>
            </div>
        )} />
    )
}

export default HighlightCard