import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import HighlightCards from '../HighlightCards/HighlightCards';

const NewsOfInterestWrapper = ({ title, subtitle = null }) => {
    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(
                sort: {order: DESC, fields: fileAbsolutePath},
                filter: {fields: {slug: {regex: "/sections/news-of-interest/"}}}
            ) {
                nodes {
                id
                frontmatter {
                    title
                    featuredImage {
                        childImageSharp {
                            fluid(maxWidth: 800) {
                            ...GatsbyImageSharpFluid
                            }
                        }
                    }
                    button_text
                    button_destination
                }
                html
                }
            }
        }
    `);

    return (
        <HighlightCards title={title} subtitle={subtitle} data={data} />
    )
}

export default NewsOfInterestWrapper