import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import styles from './FeaturedCandidates.module.css'
import FeaturedCandidate from "../FeaturedCandidate/FeaturedCandidate";

const FeaturedCandidates = () => {
    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(
                sort: {order: ASC, fields: fileAbsolutePath},
                filter: {fields: {slug: {regex: "/candidate-summaries/"}}}
            ) {
                nodes {
                id
                frontmatter {
                    name
                    linked_in
                    featuredImage {
                        childImageSharp {
                            fluid(maxWidth: 800) {
                            ...GatsbyImageSharpFluid
                            }
                        }
                    }
                    subtitle
                    twitter
                    facebook
                    button_text
                    button_destination
                }
                html
                }
            }
        }
    `);

    const candidates = data.allMarkdownRemark.nodes

    return (
        <div className={styles.Container}>
            {candidates.map(candidate => (
                <FeaturedCandidate key={`featured-candidate-${candidate.id}`} candidate={candidate} />
            ))}
        </div>
    )
}

export default FeaturedCandidates