import React from 'react'
import styles from './HighlightCards.module.css'
import HighlightCard from '../HighlightCard/HighlightCard'

const HighlightCards = ({ title, subtitle=null, data }) => {
    if (!(data && data.allMarkdownRemark && data.allMarkdownRemark.nodes && data.allMarkdownRemark.nodes.length)) {
        return null
    }
    const cards = data.allMarkdownRemark.nodes;

    return (
        <>
            <h1 className={styles.Title}>{title}</h1>
            {subtitle && (
                <h2 className={styles.Subtitle}>{subtitle}</h2>
            )}
            <div className={styles.Container}>
                {cards.map(card => <HighlightCard key={card.id} card={card} />)}
            </div>
        </>
    )
}

export default HighlightCards