import React from "react"
import styles from './HomepageHeroActions.module.css'
import PrimaryButton from "../Buttons/PrimaryButton";
import SecondaryButton from "../Buttons/SecondaryButton";

const HomepageHeroActions = ({ slide }) => {
    const options = slide.frontmatter;

    return (
        <div className={styles.Container}>
            {options.button1_text && options.button1_destination && (
                <PrimaryButton text={options.button1_text} destination={options.button1_destination} target={'_blank'} />
            )}
            {options.button2_text && options.button2_destination && (
                <SecondaryButton text={options.button2_text} destination={options.button2_destination} target={'_blank'} />
            )}
        </div>
    )
}

export default HomepageHeroActions