import React from 'react'
import { graphql, useStaticQuery, Link } from 'gatsby'
import styles from './FeaturedCandidate.module.css'
import PrimaryButton from '../Buttons/PrimaryButton'
import ButtonGroup from '../Buttons/ButtonGroup'

const FeaturedCandidate = ({ candidate }) => {
    const options = candidate.frontmatter
    const featuredImg = options.featuredImage && options.featuredImage.childImageSharp.fluid.src

    const blankImgData = useStaticQuery(graphql`
        query {
            allFile(filter: {
                sourceInstanceName: { eq: "src" }
                relativePath: { eq: "content-management/images/candidates/blank.png" }
            }) {
                edges {
                    node {
                    childImageSharp {
                        fluid {
                        ...GatsbyImageSharpFluid
                        }
                    }
                    }
                }
            }
        }
    `)

    const blankImgSrc = blankImgData.allFile.edges[0].node.childImageSharp.fluid.src

    const renderImage = (noLink = false) => {
        if (!noLink && options.button_destination) {
            return (
                <Link to={options.button_destination} className={styles.ImageLink}>
                    {renderImage(true)}
                </Link>
            )
        }

        return (
            <img src={featuredImg || blankImgSrc} alt="Candidate photo" className={styles.Image} />
        )
    }

    return (
        <div className={styles.Candidate}>
            {renderImage()}
            {options.name && (
                <h2 className={styles.Name}>{options.name}</h2>
            )}
            {options.subtitle && (
                <h3 className={styles.Subtitle}>{options.subtitle}</h3>
            )}
            {options.button_text && options.button_destination && (
                <PrimaryButton text={options.button_text} destination={options.button_destination} square />
            )}
            {candidate.html && (
                <div dangerouslySetInnerHTML={{ __html: candidate.html }} />
            )}
            <ButtonGroup>
                {options.linked_in && (
                    <PrimaryButton text={'LinkedIn'} destination={options.linked_in} target="_blank" square />
                )}
                {options.facebook && (
                    <PrimaryButton text={'Facebook'} destination={options.facebook} target="_blank" square />
                )}
                {options.twitter && (
                    <PrimaryButton text={'Twitter'} destination={options.twitter} target="_blank" square />
                )}
            </ButtonGroup>
        </div>
    )
}

export default FeaturedCandidate