import React, { useState } from "react"
import Slider from 'react-animated-slider'
import { useStaticQuery, graphql } from "gatsby"
import styles from './HomepageHero.module.css'
import HomepageHeroActions from "../HomepageHeroActions/HomepageHeroActions"

const HomepageHero = () => {
    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(filter: {fields: {slug: {regex: "/slideshow/"}}}) {
                    nodes {
                    id
                    fields {
                        slug
                    }
                    frontmatter {
                        title
                        subtitle
                        button1_text
                        button1_destination
                        button2_text
                        button2_destination
                        featuredImage {
                            childImageSharp {
                                fluid {
                                ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
    `);

    const slides = (data.allMarkdownRemark && data.allMarkdownRemark.nodes) || [];

    if (!slides.length) {
        throw new Error('No slides in slideshow.')
    }
    
    const [currentSlide, setCurrentSlide] = useState(slides[0]);

    const onSlideChange = (e) => {
        setCurrentSlide(slides[e.slideIndex]);
    }

    return (
        <>
            <Slider
                autoplay={3000}
                touchDisabled={true}
                {...(slides.length ? null : {
                    previousButton: null,
                    nextButton: null,
                })}
                onSlideChange={onSlideChange}
                infinite={true}
                classNames={{
                    slider: `slider ${styles.Slider}`,
                    slide: `slide ${styles.Slide}`,
                    nextButton: `nextButton ${styles.NextButton}`,
                    previousButton: `previousButton ${styles.PreviousButton}`,
                }}
            >
                {slides.map(slide => (
                    <div key={`homepage-slide-${slide.id}`} style={{
                        background: `URL('${slide.frontmatter.featuredImage.childImageSharp.fluid.src}') no-repeat center center fixed`,
                        backgroundSize: 'cover',
                    }}>
                        <div className={styles.Inner}></div>
                    </div>
                ))}
            </Slider>
            <div className={styles.SlideContent}>
                <h1>{currentSlide.frontmatter.title}</h1>
                <h2>{currentSlide.frontmatter.subtitle}</h2>
                <HomepageHeroActions slide={currentSlide} />
            </div>
        </>
    )
}

export default HomepageHero