import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'

const MailIconProvider = ({ render }) => {
    const data = useStaticQuery(graphql`
        query {
            allFile(filter: {
                sourceInstanceName: { eq: "src" }
                relativePath: { eq: "content-management/images/general/mail-icon-lg.png" }
            }) {
                edges {
                    node {
                    childImageSharp {
                        fluid {
                        ...GatsbyImageSharpFluid
                        }
                    }
                    }
                }
            }
        }
    `)

    const mailIcon = data.allFile.edges[0].node.childImageSharp.fluid.src

    return (
        <>
            {render(mailIcon)}
        </>
    )
}

export default MailIconProvider