import React from 'react'
import MailIconProvider from '../ImageProviders/MailIconProvider'
import { graphql, useStaticQuery } from 'gatsby'
import PrimaryButton from '../Buttons/PrimaryButton';
import styles from './ContactBlock.module.css'

const ContactBlock = () => {
    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(
                sort: {order: ASC, fields: fileAbsolutePath},
                filter: {fields: {slug: {regex: "/sections/contact-block/"}}}
            ) {
                nodes {
                id
                frontmatter {
                    title
                    email_address
                }
                html
                }
            }
        }
    `);

    const mailBlocks = data.allMarkdownRemark.nodes

    return (
        <MailIconProvider render={(mailIcon) => (
            <div className={styles.ContactBlock}>
                <h1>We want to Hear from You! Contact Us!</h1>
                <p>Reach out to us – We are waiting to Hear from You! Questions about the Party? Interested in Volunteering?</p>
                <div className={styles.MailContainer}>
                    {mailBlocks.map(mailBlock => (
                        <div key={`mailblock-${mailBlock.id}`} className={styles.MailBlock}>
                            <img src={mailIcon} alt="mail icon" />
                            <h2>{mailBlock.frontmatter.title}</h2>
                            <PrimaryButton text={'Click here to send an email'} destination={`mailto:${mailBlock.frontmatter.email_address}`} target='_blank' square />
                            <p>{mailBlock.frontmatter.email_address}</p>
                        </div>
                    ))}
                </div>
            </div>
        )} />
    )
}

export default ContactBlock